import React, { useEffect, useRef } from 'react'
import styled from '@emotion/styled'
import { animOne } from '../animations/anim1'

const StyledCanvas = styled.canvas`
  // width: '100px';
  // height: '100px';
`

interface RCanvasProps {
  animation: (canvasRef: React.RefObject<HTMLCanvasElement>) => () => void
}

const RCanvas: React.FC<RCanvasProps> = ({ animation }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null)

  useEffect(() => {
    // return animOne(canvasRef)
    return animation(canvasRef)
  })

  return <StyledCanvas ref={canvasRef} height="300px" width="200px" />
}

export default RCanvas
